import Dict from '@baifendian/adhere-util-dict';
export default {
    initStatic: function () {
        Dict.handlers.ResourceGisEpsg4326 = function () { return 'EPSG:4326'; };
        Dict.handlers.ResourceGisEpsg3857 = function () { return 'EPSG:3857'; };
        Dict.handlers.ResourceGisEarthRadius = function () { return 6378137.0; }; // 单位M
        Dict.handlers.ResourceGisMapMaxZoom = function () { return 17; };
        Dict.handlers.ResourceGisMapMinZoom = function () { return 11; };
        // 常州市新北区地图的范围
        Dict.handlers.ResourceGisXinbeiquMapExtent = function () { return [
            [119.438, 32.13607],
            [120.33419, 31.74221],
        ]; };
        // 常州市新北区中心点
        Dict.handlers.ResourceGisXinbeiquCenterPoint = function () { return [119.879673, 31.933156]; };
    },
    initRemote: function () { },
};
